// 
export interface ComponentType {
  id: string;
  label: string;
  icon: string;
  description: string;
}

export interface ChartType {
  id: string;
  label: string;
  icon: string;
}

// 
export const recordComponentTypes: ComponentType[] = [
  { id: 'text-short', label: '', icon: '', description: '' },
  { id: 'text-long', label: '', icon: '', description: '' },
  { id: 'date', label: '', icon: '', description: '' },
  { id: 'number', label: '', icon: '', description: '' },
  { id: 'image', label: '', icon: '', description: '' },
  { id: 'video', label: '', icon: '', description: '' },
  { id: 'audio', label: '', icon: '', description: '' },
  { id: 'file', label: '', icon: '', description: '' },
];

// 
export const analysisComponentTypes: ComponentType[] = [
  { id: 'ai-custom', label: 'AI', icon: '', description: 'AI' },
  { id: 'chart', label: '', icon: '', description: '' },
];

// 
export const chartTypes: ChartType[] = [
  { id: 'bar', label: '', icon: '' },
  { id: 'bubble', label: '', icon: '' },
  { id: 'gantt', label: '', icon: '' },
];

// 
export const getComponentTitle = (componentType: string): string => {
  const allComponents = [...recordComponentTypes, ...analysisComponentTypes];
  const component = allComponents.find(c => c.id === componentType);
  return component ? component.label : '';
};

// 
export const getComponentInfo = (componentType: string): ComponentType | undefined => {
  const allComponents = [...recordComponentTypes, ...analysisComponentTypes];
  return allComponents.find(c => c.id === componentType);
};
